@import '../variables.scss';

.progress {
    display: block;
    width: $window-width-xl;
    height: 36px;

    background-color: #035d11;
    border-radius: 12px;

    overflow: hidden;

    &-bar {
        background-color: #27ed44;
        height: 100%;
        border-radius: 12px;
        transition: all 1.5s;
    }

    @media (max-width: 992px) {
        width: $window-width-l;
    }
    @media (max-width: 576px) {
        width: $window-width-m;
    }
    @media (max-width: 450px) {
        width: $window-width-s;
    }
}