@import '../variables.scss';

.modal-outer {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;

    display: flex;
    justify-content: center;
    align-items: center;
    
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 10;
}

.modal {
    display: block;
    width: 800px;
    min-height: 400px;
    padding: $padding-inner;

    font-size: 20px;
    color: #000;

    background-color: $modal-bg;
    background: linear-gradient(rgba($modal-bg, 0.2), rgba($modal-bg, 0.2)), url('../../resources/paper.jpeg');
    border-radius: 12px;

    p {
        font-size: 24px;
        font-weight: 500;
    }

    button {
        padding: 10px;
        min-width: 150px;
        font-size: 24px;
        font-weight: 700;
        border-radius: 12px;
        border: none;
        box-shadow: 0 0 5px rgb(255, 0, 0);
        cursor: pointer;
    }

    @media (max-width: 450px) {
        width: 100%;
        height: 100vh;
        border-radius: 0;
    }

    &__img {
        display: block;
        margin: 0 auto;
        margin-bottom: 20px;
        max-width: 100%;
        max-height: 50vh;
        
        object-fit: contain;
        border-radius: 12px;
        box-shadow: 0 0 2px #000;
    }
}