@import '../variables.scss';

.multy-question {
    display: block;
    width: $window-width-xl;
    padding: $padding-inner;
    margin-top: $gap;
    margin-bottom: 20px;

    font-size: 20px;
    color: #000;

    background-color: $light-bg;
    border-radius: 12px;

    @media (max-width: 450px) {
        padding: 20px;
    }

    &__caption {
        margin-top: 20px;
    }

    &__wrapper {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 10px;
        padding-inline-start: 0;

        @media (max-width: 450px) {
            grid-template-columns: 1fr;
            margin-top: 0;
        }
    }

    &__item {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 40px;
        background-color: $main-bg;
        transform: rotate3d(10, 0, 0, 180deg);
        transition: transform 2s;
        color: #fff;

        span {
            display: none;

            &.span-active {
                display: block;
            }
        }

        &_showed {
            transform: rotate3d(10, 0, 0, 0deg);
        }
    }

    @media (max-width: 992px) {
        width: $window-width-l;
    }
    @media (max-width: 576px) {
        width: $window-width-m;
    }
    @media (max-width: 450px) {
        width: $window-width-s;
        margin-top: $gap-s;
    }
}

.multy-question__item:has(span.span-active) {
    background-color: red;
}