@import '../variables.scss';

.end-point {
    display: block;
    width: $window-width-xl;
    padding: $padding-inner;
    margin-top: $gap;

    font-size: 20px;
    color: #000;

    background-color: $light-bg;
    border-radius: 12px;

    @media (max-width: 992px) {
        width: $window-width-l;
    }
    @media (max-width: 576px) {
        width: $window-width-m;
    }
    @media (max-width: 450px) {
        width: $window-width-s;
        margin-top: $gap-s;
    }
}