@import '../variables.scss';

.decor {
    position: absolute;
    top: 82px;
    left: calc($window-width-xl + ((100vw - $window-width-xl - 20px)/2));
    // right: 22%;

    display: block;
    width: 140px;

    z-index: 9;

    &__img {
        width: 100%;
    }

    &__msg {
        display: block;
        padding: 6px;
        background-color: red;
        position: absolute;
        right: 120px;
        top: -30px;
        border-radius: 12px;
        font-size: 14px;
        width: 240px;
        text-align: left;

        &::before {
            display: block;
            content: '';
            position: absolute;
            bottom: -10px;
            right: -10px;
            width: 20px;
            height: 20px;
            border-radius: 100%;
            background-color: red;
        }

        @media (max-width: 992px) {
            right: 100px;
        }
        @media (max-width: 576px) {
            right: 80px;
        }
        @media (max-width: 450px) {
            width: 260px;
            top: -15px;
            right: 95px;
        }
    }

    &__msg_animated {
        animation: msg_anim 300ms linear 0s 2;
    }

    @media (max-width: 992px) {
        width: 120px;
        left: calc($window-width-l + ((100vw - $window-width-l - 80px)/2));
    }
    @media (max-width: 576px) {
        width: 100px;
        left: calc($window-width-m + ((100vw - $window-width-m - 100px)/2));
    }
    @media (max-width: 450px) {
        top: 70px;
        left: calc($window-width-s + ((100vw - $window-width-s - 100px)/2));
    }
}

@keyframes msg_anim {
    from {
        transform: skewY(2deg);
    }
    10% {
    }
    50% {
        transform: skewY(-2deg);
    }
    90% {
    }
    to {
        transform: skewY(2deg);
    }
}