@import '../variables.scss';

.img-question {
    display: block;
    width: $window-width-xl;
    padding: $padding-inner;
    margin-top: $gap;
    margin-bottom: 20px;

    font-size: 20px;
    color: #000;

    background-color: $light-bg;
    border-radius: 12px;

    &__wrapper {
        display: grid;
        grid-template-columns: 1fr 1fr;
        overflow: hidden;
        gap: 4px;
    }

    &__img {
        display: block;
        object-fit: cover;
        width: 100%;
        height: 100%;
        max-height: 240px;
    }

    &__img-wrap {
        position: relative;
        width: 100%;
        height: 100%;

        &:before {
            content: '';
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            top: 10px;
            left: 10px;
            width: 40px;
            height: 40px;
            background-color: #cd0808;
            border-radius: 100%;
            font-size: 30px;
            font-weight: 600;
            color: #fff;
            box-shadow: 0 0 10px #000;

            @media (max-width: 450px) {
                width: 30px;
                height: 30px;
                font-size: 24px;
            }
        }

        &:nth-of-type(1):before {
            content: "A";
        }
        &:nth-of-type(2):before {
            content: "B";
        }
        &:nth-of-type(3):before {
            content: "C";
        }
        &:nth-of-type(4):before {
            content: "D";
        }
    }

    &__caption {
        margin-top: $padding-inner;
        font-size: 14px;
        line-height: 1.2;
        text-align: left;

        span {
            font-weight: 700;
        }
    }

    @media (max-width: 992px) {
        width: $window-width-l;
    }
    @media (max-width: 576px) {
        width: $window-width-m;
    }
    @media (max-width: 450px) {
        width: $window-width-s;
        margin-top: $gap-s;
    }
}