@import '../variables.scss';

.user-interface {
    display: block;
    width: $window-width-xl;
    padding: $padding-inner;
    margin-top: $gap;

    font-size: 20px;
    color: #000;

    background-color: $light-bg;
    border-radius: 12px;

    @media (max-width: 450px) {
        margin-top: auto;
    }

    &__buttons {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 4px;
        border-radius: 12px;
        overflow: hidden;
        // background-color: $accent-dark;
    }

    &__buttons-item {
        display: block;
        padding: 10px;

        font-size: 20px;
        font-weight: 600;
        color: #000;


        background-color: $accent-main;
        border: none;

        transition: all .4s;
        cursor: pointer;

        @media (max-width: 450px) {
            padding: 8px;
            font-size: 16px;
        }

        @media (min-width: 576px) {
            &:hover {
                color: $light-bg;
                background-color: $accent-additional;
            }
        }
    }

    &__input {
        display: flex;
        justify-content: center;

        @media (max-width: 450px) {
            gap: 10px;
            flex-direction: column;
        }
    }

    &__input-item {
        font-size: 20px;
        padding: 10px;
        width: 250px;
        border: solid 1px $accent-additional;

        @media (max-width: 450px) {
            width: 100%;
        }
    }

    &__input-btn {
        padding: 10px;

        font-size: 20px;
        color: #000;

        background-color: $accent-main;
        border: solid 1px $accent-additional;
        
        transition: all .4s;
        cursor: pointer;

        @media (min-width: 576px) {
            &:hover {
                background-color: $accent-additional;
                color: #fff;
            }
        }
    }

    @media (max-width: 992px) {
        width: $window-width-l;
    }
    @media (max-width: 576px) {
        width: $window-width-m;
    }
    @media (max-width: 450px) {
        width: $window-width-s;
    }
}