@import '../variables.scss';

.text-question {
    display: block;
    width: $window-width-xl;
    padding: $padding-inner;
    margin-top: $gap;
    margin-bottom: 20px;

    font-size: 20px;
    color: #000;
    text-align: left;

    background-color: $light-bg;
    border-radius: 12px;

    @media (max-width: 992px) {
        width: $window-width-l;
    }
    @media (max-width: 576px) {
        width: $window-width-m;
    }
    @media (max-width: 450px) {
        width: $window-width-s;
        margin-top: $gap-s;
        // padding-right: 40px;
    }

    &__img {
        display: block;
        width: 100%;
        max-height: 50vh;
        object-fit: cover;
        border-radius: 4px;
    }
}